.profile__avatar {
    margin: 0 auto;
    max-width: 120px;
    max-height: 120px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 50%;
    background-position: center center;
    background-size: cover;
}

@media (min-width: 650px) {
    .profile__avatar {
        margin: 0;
    }
}